@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One|Montserrat);
body {
  font-family: 'Monserrat', sans-serif;
  font-family: var(--font-primary), sans-serif;
  background-color: #FDF8F4;
  background-color: var(--color1);
  color: #4E4E4E;
  color: var(--color5);
}

a {
  color: #4E4E4E;
  color: var(--color5);
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

.secondary {
 text-transform: uppercase;
 font-family: 'Julius Sans One', 'Courier New', Courier, monospace;
 font-family: var(--font-secondary), 'Courier New', Courier, monospace; 
 font-weight: 100;
}
.layout-center {
  max-width: 1000px; 
  margin-left: auto;
  clear: both;
}

.Header {
  float: right;
  top: -10vh;
  width: 100%;
  height: 10vh;
  min-height: 70px;
  background-color: #FDF8F4;
  background-color: var(--color1);
  color: #4E4E4E;
  color: var(--color5);
  transition: 
  background-color cubic-bezier(.45,.05,.55,.95) 1s,
  top ease 1s;
}
.Header--Gone {
  display: none;
}
.Header--Hidden {
  position: fixed;
  top: -10vh;
  transition: 
  top ease 1s,
  box-shadow ease 1s;
  background-color: #4E4E4E;
  background-color: var(--color5);
  color: #FDF8F4;
  color: var(--color1);
}
.Header--Sticky {
  position: fixed;
  top: 0vh;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .2);
  background-color: #4E4E4E;
  background-color: var(--color5);
  color: #FDF8F4;
  color: var(--color1);
}


.Logo {
  -webkit-flex: 1 0 60%;
          flex: 1 0 60%;
}
.Logo__Border {
  width: 3rem;
  height: 3rem;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.Navigation {
  margin: 0;
  -webkit-flex: 1 0 40%;
          flex: 1 0 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
}
.Navigation__Item {
  list-style: none;
  margin-right: 16px;
}

.Light{
  width: 2rem;
  fill: #FF5A5F;
  fill: var(--color2);
}

@media screen and (max-width: 30em) {
  .Navigation, .Header--Sticky, .Header--Hidden {
    display: none;
  }
}


.Intro{
  height: 100vh;
  background-image: url(/portfolio/static/media/background-light.8f613109.jpg);
  background-size: cover;
  background-attachment: fixed;
  transition: background-image 0.2s ease-in-out;
}
@media screen and (max-width: 30em) {
  .Intro {
    background-position: -310px 0;
    background-size: 1150px;
  }
}


.Intro--flex{
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Post {
  margin-top : 3rem;
}


#Projects {
  background-color: #FF5A5F;
  background-color: var(--color2);
  
}
.Project {
  -webkit-flex: 1 0 100%;
          flex: 1 0 100%;
}

@media screen and (min-width: 40em) {
  .Project {
    -webkit-flex: 1 0 50%;
            flex: 1 0 50%;
    min-height: 300px;
  }
}

@media (--breakpoint-medium) {
  .Project {
    -webkit-flex: 1 0 50%;
            flex: 1 0 50%;
  }
}

@media (--breakpoint-large) {
  .Project {
    -webkit-flex: 1 0 50%;
            flex: 1 0 50%;
  }
}


.About {
  min-height: 50vh;
}

:root {
  --color1: #FDF8F4;
  --color2: #FF5A5F;
  --color3: #DAD9D7;
  --color4: #939393;
  --color5: #4E4E4E;

  --font-primary: 'Monserrat';
  --font-secondary: 'Julius Sans One';
}

/* ATOMIC */
.color1 {color: #FDF8F4;color: var(--color1);fill: #FDF8F4;fill: var(--color1)}
.color2 {color: #FF5A5F;color: var(--color2);fill: #FF5A5F;fill: var(--color2)}
.color3 {color: #DAD9D7;color: var(--color3);fill: #DAD9D7;fill: var(--color3)}
.color4 {color: #939393;color: var(--color4);fill: #939393;fill: var(--color4)}
.color5 {color: #4E4E4E;color: var(--color5);fill: #4E4E4E;fill: var(--color5)}

.bg-color1 {background-color: #FDF8F4;background-color: var(--color1)}
.bg-color2 {background-color: #FF5A5F;background-color: var(--color2)}
.bg-color3 {background-color: #DAD9D7;background-color: var(--color3)}
.bg-color4 {background-color: #939393;background-color: var(--color4)}
.bg-color5 {background-color: #4E4E4E;background-color: var(--color5)}


.Slider {
  display: grid-inline;
  height: 6rem;
  overflow: hidden;
}

.Slider__Box {
  height: 6rem;
  grid-row-start: 1;
  grid-row-end: 1;
  opacity: 1;
  transition: margin-top ease 3s, opacity linear 1s;
}

.Slider__Box--hide {
  margin-top: -6rem;
  opacity: 0;
}
